<template>
  <div class="login">
    <vue-particles class="login-bg"
                   color='#39AFFD'
                   :particleOpacity="0.7"
                   :particlesNumber="100"
                   shapeType="circle"
                   :particleSize="4"
                   linesColor="#8DD1FE"
                   :linesWidth="1"
                   :lineLinked="true"
                   :lineOpacity="0.4"
                   :linesDistance="150"
                   :moveSpeed="3"
                   :hoverEffect="true"
                   hoverMode="grab"
                   :clickEffect="true"
                   clickMode="push">
    </vue-particles>
    <!-- 登录面板 -->
    <div class="content">
      <div class="content_input">
        <div class="title">
          <p>用户登录</p>
        </div>
        <el-input v-model="UserName"
                  placeholder="用户名"></el-input>
        <el-input v-model="PassWord"
                  placeholder="密码"
                  show-password></el-input>
        <div class="content_button">
          <el-button type="primary"
                     @click="SignIn">登录</el-button>
        </div>
      </div>
    </div>

  </div>

</template>

<script>

export default {
  data () {
    return {
      UserName: '',
      PassWord: ''
    }
  },
  methods: {
    SignIn () {
      let that = this;
      let username = that.UserName;
      let password = that.PassWord;
      if (!username) {
        this.$notify.error({
          title: '错误',
          message: '用户名不能为空'
        });
        return;
      } else if (!password) {
        this.$notify.error({
          title: '错误',
          message: '密码不能为空'
        });
        return;
      } else {
        this.$message({
          message: '登陆成功，欢迎进入',
          type: 'success'
        });
        setTimeout(() => {
          this.$notify({
            title: '没有服务端啊',
            message: '自己去其他页面逛逛吧',
            position: 'top-left'
          });
        }, 2000)
      }
    }
  }
}

</script>

